.App {
  padding: 50px;
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  border-left: solid black 1px;
  border-right: solid black 1px;
  min-height: 100vh;
  background-color: #BDEAFF;

}

table, th, td {
  /*border-collapse: collapse;*/
}

td {
  border: 1px solid black;
  color: rgba(0, 0, 0, 0);
  /*color: rgba(255, 0, 0, 0.7);*/
  padding: 5px;
  text-align: center;
  background-color: whitesmoke;
}

button {
  background-color: white;
  color: #00B9FF;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}

button:hover {
  background-color: #F0F0F0;
  cursor: pointer;
}


.lyricSpace {
  position: fixed;
  background-color: #BDEAFF;
  padding: 20px;
}

#lyricCheck {
  padding: 5px;
  font-size:16px;
  width: 300px;
  
}